  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  -webkit-overflow-scrolling: touch; }
  .js-modal-open .modal {
    overflow-x: hidden;
    overflow-y: auto; }
  .modal__dialog {
    position: relative;
    margin: 0;
    width: 100%;
    height: 100%; }
    @media (min-width: 992px) {
      .modal__dialog {
        width: 600px;
        height: auto; }
        .modal--open .modal__dialog {
          animation: modal-content-show 0.3s 1 both; } }
  .modal__content {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 100%;
    min-height: 100%;
    background-color: #fff;
    outline: 0; }
  .modal__header {
