    color: currentColor; }
  .modal__close {
    position: absolute !important;
    top: 5px;
    right: 5px; }
  .modal__body {
    padding: 0 10px;
    flex-grow: 1; }
  .modal__footer {
    padding: 10px;
    flex-shrink: 0; }
  .modal__backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.7); }
    @media (min-width: 992px) {
      .modal__backdrop {
        animation: modal-backdrop-show 0.3s 1; } }
  @media (min-width: 992px) {
    .modal--open {
      display: flex;
      justify-content: center;
      align-items: center; } }

@keyframes modal-backdrop-show {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes modal-content-show {
  0% {
    opacity: 0;
    transform: translateY(-2em); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

.close {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  width: 30px;
  height: 30px;
  user-select: none;
  cursor: pointer; }
  .close > span {
    display: inline-block;
    width: 100%;
    height: 3px;
    background: transparent;
    position: relative; }
    .close > span:before, .close > span:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background: #000;
      transform-origin: 50% 50%; }
    .close > span:before {
      transform: rotate3d(0, 0, 1, 45deg); }
    .close > span:after {
      transform: rotate3d(0, 0, 1, -45deg); }

